<template>
    <div class="login">
        <h3>会员注册</h3>
        <span>账号：<input type="text" value="请输入账号(必填)" class="ipt1" v-model="username"/></span>
        <span>密码：<input type="text" value="请输入密码(必填)" class="ipt1" v-model="password"/></span>
        <span>密码：<input type="text" value="请再次输入密码(必填)" class="ipt1"/></span>
        <span>姓名：<input type="text" value="请输入名称(必填)" class="ipt1"/></span>
        <span>邮箱：<input type="text" value="请输入邮箱(必填)" class="ipt1"/></span>
        <span>手机：<input type="text" value="请输入手机号码" class="ipt1"/></span>
        <span>备注：<input type="text" value="请输入备注(选填)" class="ipt1" /></span>
        <input type="button" value="注册" class="ipt2" @click="reg"/><br/>
    </div>
</template>

<style scoped>
    .login h3{font: .5rem/1.3rem '';width: 100%;border-bottom:.1rem solid whitesmoke;text-align: center;}
    .login span{display: block;font:.4rem/1.5rem '';padding-left: 1rem;border-bottom:.1rem solid whitesmoke;color:#333333;}
    .login span input.ipt1{color:#d6dada;}
    .login input.ipt2{font: .4rem/1rem '';padding:0 3rem;background:#3285ff;
    color: honeydew;border-radius: .2rem;margin:.8rem 0 0 1.7rem;}
    .login a{display: inline-block;font: .35rem/.5rem '';margin:.8rem 0 0 4.3rem;}
</style>

<script>
    export default {
        data(){
            return {
                username:'',
                password:'',
                mess:''
            }
        },
        methods:{
            reg(){
            let formData = new FormData();
            formData.append('username',this.username)
            formData.append('password',this.password)
            // formData.append('icon',this.$refs.f1.files[0])

            axios({
                url:'/api/reg',
                method:'post',
                data:formData
            }).then(
                res => res.data.err===0 ? this.$router.push('/login') : this.mess = res.data.msg
            )

            }
        }
    }
</script>
